















import { Component, Vue, Prop, Watch } from 'vue-property-decorator'
import wx from 'weixin-js-sdk'
import { formatDate } from '@/until/index'

@Component({
  components: {

  },
  filters: {
   
  }
})
export default class accountSelect extends Vue {

  accountList = []
  accountId = ''

  activated () {
    if (this.accountId) {
      this.$emit('change', this.accountId)
    } else {
      this.pageInit()
    }
  }

  // 计算属性
  get computedMsg () {
    return 'computed ' + this.propB
  }
  get accountOptions () {
    this.accountList.forEach((obj) =>{
      obj.value = obj.id
      obj.text = obj.name
    })
    return this.accountList
  }
  getBindingInfo () {
    let query = this.$route.query
    if (query.code) {
      const toast = this['$createToast']({
        time: 0,
        txt: '数据获取中...'
      })
      toast.show()
      this.$store.dispatch('getCodeInfo', { code: query.code }).then((response) => {
        if (response.success) {
          if (response.data.openid) {
            this.openid = response.data.openid
            this.$router.replace({ path: '/login', query: { openid: response.data.openid, jump: '/home' } })
          } else if (response.data.sessionId) {
            this.$store.commit('SET_SESSION_ID', response.data.sessionId)
            this.$store.commit('SET_USER_INFO', response.data.userInfo)
            this.getAdminAccountList()
          }
        }
        toast.hide()
      })
    } else {
      console.log('不是微信登陆')
    }
  }
  pageInit () {
    let query = this.$route.query
    if (this.$store.state.sessionId && !this.accountId) {
      this.getAdminAccountList()
    } else if (query.code && !this.$store.state.sessionId) {
      this.getBindingInfo()
    }
  }

  getAdminAccountList () {
    var that = this
    this.$store.dispatch('getAdminAccountList', {}).then((response) => {
      if (response.data.list) {
        response.data.list.forEach((item, index) => {
          that.$set(that.accountList, index, item)
        })
        if (!this.accountId) {
          this.accountId = this.accountList[0].id
        }
      }
    })
  }
  getFloorByAccountId () {
    const toast = this['$createToast']({
      time: 0,
      txt: '数据获取中...'
    })
    toast.show()
    this.$store.dispatch('getFloorByAccountId', { accountId: this.accountId }).then((response) => {
      if (response.success) {
        if (this.selectedLabelDefault === 'incomplete') {
          this.billList = response.data.list
          this.initCheckbox()
        } else {
          if (response.data.list.length > 0) {
            this.billList = this.billList.concat(response.data.list)
          } else {
            this.isShowMore = false
          }
        }
      }
      toast.hide()
    })
  }

  @Watch('accountId')
  accountChange (id: any) {
    this.$emit('change', id)
  }
}
