





























import { Component, Vue, Prop, Watch } from 'vue-property-decorator'
import accountSelect from '@/components/common-components/accountSelect'
import wx from 'weixin-js-sdk'
import { formatDate, isHasFunc } from '@/until/index'

@Component({
  components: {
    accountSelect
  },
  filters: {
    fenToYuan (num: any) {
      if (typeof num !== 'number' || isNaN(num)) return null
      return '¥' + (num / 100).toFixed(2)
    }
  }
})
export default class payAdmin extends Vue {
  raceLength = 0
  raceCost = 0
  accountList = []
  accountId = ''

  // 计算属性
  get computedMsg () {
    return 'computed ' + this.propB
  }
  get accountOptions () {
    this.accountList.forEach((obj) =>{
      obj.value = obj.id
      obj.text = obj.name
    })
    return this.accountList
  }

  getAccountId (id: any) {
    this.accountId = id
  }
  toOtherPage (type: any) {
    if (type) {
      let path = '/' + type
      this.$router.push({ path: path, query: { accountId: this.accountId} })
    }
  }
  getPower (key: any) {
    let user = this.$store.state.userInfo
    return isHasFunc(key, user)
  }
  get showAdmin () {
    if (this.$store.state.userInfo && this.$store.state.userInfo.power && this.$store.state.userInfo.power.length > 0) {
      return this.$store.state.userInfo.adminAccount.indexOf(this.accountId) > -1
    } else {
      return false
    }
  }
}
